@import "/src/components/globalcss/color.scss";
.main {
  .status {
    display: flex;
    flex-flow: column nowrap;
    align-content: center;
    text-align: left;
    align-items: center;
    // flex: 0 1;
    text-transform: uppercase;
    font-family: "operatormomo";
    white-space: nowrap;
    .icon {
      align-self: center;
      overflow: hidden;
      height: 1rem;
      margin-bottom: 0.5rem;
      overflow: hidden;
    }
  }
  a {
    width: 100%;
    vertical-align: middle;
    text-align: center;
    overflow: hidden;
    position: relative;
    display: inline-block;
    margin: .5rem 0 0;
    padding:.1rem 0;
    background: transparentize($yellow, 0.3);
    border-radius: .2rem;
    font-size: .8rem;
    text-decoration: none;
    text-transform: uppercase;
    font-family: "operatormomo";
    white-space: nowrap;
    transition: 0.5s;
    letter-spacing: 0.1rem;
    -webkit-box-reflect: below 0.3rem
      linear-gradient(transparent, rgba(5, 5, 5, 0.333));
    &:hover {
      background: $yellow;
      box-shadow: 0 0 5px rgba(invert($yellow), 50%),
        0 0 25px rgba(invert($yellow), 40%), 0 0 50px rgba(invert($yellow), 20%);
    }
    .myspan {
      position: absolute;
      display: block;
      &:nth-child(1) {
        top: 0;
        left: -100%;
        width: 100%;
        height: 2px;
        background: linear-gradient(90deg, transparent, invert($yellow));
        animation: animate1 1s linear infinite;
      }
      &:nth-child(2) {
        top: -100%;
        right: 0;
        width: 2px;
        height: 100%;
        background: linear-gradient(180deg, transparent, invert($yellow));
        animation: animate2 1s linear infinite;
        animation-delay: 0.25s;
      }
      &:nth-child(3) {
        bottom: 0;
        right: -100%;
        width: 100%;
        height: 2px;
        background: linear-gradient(270deg, transparent, invert($yellow));
        animation: animate3 1s linear infinite;
        animation-delay: 0.5s;
      }
      &:nth-child(4) {
        bottom: -100%;
        left: 0;
        width: 2px;
        height: 100%;
        background: linear-gradient(360deg, transparent, invert($yellow));
        animation: animate4 1s linear infinite;
        animation-delay: 0.75s;
      }
    }
  }
}

@keyframes animate1 {
  0% {
    left: -100%;
  }
  50%,
  100% {
    left: 100%;
  }
}
@keyframes animate2 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}
@keyframes animate3 {
  0% {
    right: -100%;
  }
  50%,
  100% {
    right: 100%;
  }
}
@keyframes animate4 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}

@keyframes broken {
  0% {
    clip-path: var(--slice-0);
    transform: translate(5px, 0px);
  }
  10% {
    clip-path: var(--slice-2);
    transform: translate(-5px, 0px);
  }
  20% {
    clip-path: var(--slice-5);
    transform: translate(5px, 10px);
  }
  30% {
    clip-path: var(--slice-3);
    transform: translate(-10px, 0px);
  }
  40% {
    clip-path: var(--slice-4);
    transform: translate(0px, -5px);
  }
  50% {
    clip-path: var(--slice-1);
    transform: translate(5px, -10px);
  }
  60% {
    clip-path: var(--slice-5);
    transform: translate(5px, 0px);
  }
  70% {
    clip-path: var(--slice-4);
    transform: translate(-5px, 8px);
  }
  80% {
    clip-path: var(--slice-1);
    transform: translate(-2px, 4px);
  }
  90% {
    clip-path: var(--slice-3);
    transform: translate(-3px, 2px);
  }
  100% {
    clip-path: var(--slice-4);
    transform: translate(10px, 4px);
  }
}
