@import "/src/components/globalcss/color.scss";

.craft {
  display: grid;
  // 列布局: 为了让宽度适应最大内容宽度, 不使用 fr 自动填充,
  // 使用 auto (尺寸的上限是最大内容尺寸的最大值)
  // grid-template-columns: 1rem repeat(auto-fit, minmax(0, 1fr));
  // 放弃上述方案，原因是内容多少会影响宽度，导致每行的子项目宽度无法对齐，使用 fr
  // 由于子项内容不同，在屏幕变小时还是会缩小程度不同，故设置为 minmax(20%,3fr)
  grid-template-columns: 1rem 1fr 5fr 2fr minmax(20%,3fr) 1fr;
  grid-template-rows: repeat(auto-fit, auto);
  // 配合 auto 布局: space-evenly 让内容横向均匀分布
  justify-content: space-around;
  justify-items: center;
  align-items: center;
  margin: 1rem 2rem;
  font-size: 0.5rem;
  // 保证子项都有间隙
  gap: .5rem;
  .craftContent {
    background: #fff url("../../../src/components/header/nav-bg.png") round
      left/contain;
    padding: 0.1rem;
    // 保持与 grid 分配的高度一致
    height: 100%;
    width:100%;
    user-select: none;
    border-top: $yellow .5px solid;
    border-right: $yellow .5px solid
  }
  h3 {
    text-align: center;
    margin: -0.5rem 0.2rem 0.5rem;
    padding: 0.1rem;
    transform: rotate(-1deg);
    white-space:nowrap;
    span {
      background-color: $yellow;
    }
  }
  .item {
    overflow: hidden;
    // flex:1 0;
  }
  .icon {
    align-self: center;
    width: 20px;
    outline: .1rem dotted $yellow;
    overflow: hidden;
  }
  // flex 布局如何让子项宽高充满
  @mixin itemFlex {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-evenly;
    margin: 0 0.5rem 0 .5rem;
    gap: 1rem;
    
  }
  .itemRaw {
    @include itemFlex();
    
  }
  .itemAdd {
    @include itemFlex();
  }
  .itemLevel {
    @include itemFlex();
  }
  .itemScroll {
    @include itemFlex();
  }
  .itemScore {
    @include itemFlex();
  }
}

@media screen and (max-width: 1000px) {
  .craft {
    // 小屏幕下, 每个项目占据一行
    grid-template-columns: 1fr;
    gap: 1rem;
  }
}
