@import "/src/components/globalcss/color.scss";

.status {
  // flex 默认样式: 应用flex:initial的元素在flex容器有剩余空间时其尺寸不会增长
  //（flex-grow:0），在flex容器尺寸不足时尺寸会收缩变小（flex-shrink:1），同时当前应
  //用flex:initial的元素的尺寸自适应于内容（flex-basis:auto）
  display: flex;
  flex-flow: column nowrap;
  align-content: center;
  text-transform: uppercase;
  font-family: "operatormomo";
  white-space: nowrap;
  .icon {
    align-self: center;
    overflow: hidden;
    height: 1rem;
    margin-bottom: 0.5rem;
  }
}
